
import { defineComponent, ref } from 'vue'

export default defineComponent({
  setup () {
    const skills = ref([
      {
        category: 'Front-end',
        list: ['Javascript', 'HTML5', 'CSS', 'SCSS', 'JQuery', 'Vue']
      },
      {
        category: 'Back-end',
        list: ['Nodejs', 'Express', 'MongoDB']
      },
      {
        category: 'Other',
        list: ['Git', 'Visual Studio Code', 'Affinity photo']
      }
    ])
    return { skills }
  }
})
